<template>
<div>
  <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="职工号">
    <el-input v-model="form.tno"></el-input>
  </el-form-item>
  <el-form-item label="名字">
    <el-input v-model="form.tname"></el-input>
  </el-form-item>
  <el-form-item label="性别">
    <el-select v-model="form.tsex" placeholder="请选择性别">
      <el-option label="男" value="男"></el-option>
      <el-option label="女" value="女"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="年龄">
    <el-input v-model="form.tage"></el-input>
  </el-form-item>
  <el-form-item label="职称">
    <el-input v-model="form.title"></el-input>
  </el-form-item>
    <el-form-item label="联系电话">
    <el-input v-model="form.phone"></el-input>
  </el-form-item>
<el-form-item label="登录密码">
  <el-input v-model="form.code" show-password type="password"></el-input>
</el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">添加</el-button>
    <el-button @click="exit">取消</el-button>
  </el-form-item>
</el-form>
</div>
</template>
<script>
  export default {
    data() {
      return {
        form: {
          tno: '',
          tname: '',
          tsex: '',
          tage: '',
          title:'',
          phone:'',
          code:''
        }
      }
    },
    methods: {
      onSubmit() {
    // 检查表单信息完整性
    if (!this.form.tno || !this.form.tname || !this.form.tsex || !this.form.tage || !this.form.title || !this.form.phone|| !this.form.code) {
    this.$message({
      type: 'warning',
      message: '请填写完整的职工信息'
    });
    return;
   }
      let params = new URLSearchParams();
      params.append('tno', this.form.tno);
      params.append('tname', this.form.tname);
      params.append('tsex', this.form.tsex);
      params.append('tage', this.form.tage);
      params.append('title', this.form.title);
      params.append('phone', this.form.phone);
      params.append('code', this.form.code);
      
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8690/addteacher",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        if(result.data.message=="successfully"){
            this.$message({
          type: 'success',
          message: '操作成功!'
        });
          this.form.tno = '';
          this.form.tname='';
          this.form.tsex=null;
          this.form.tage='';
          this.form.title='';
          this.form.phone='';
          this.form.code=''
          
        }else{
          this.$message({
          type: 'warning',
          message: '操作失败!'
        });
        }
      }).catch((error) => {
        console.error('请求失败:', error);
      });
      },
      exit(){
         this.$router.push('/admin/teacher')
      }
    }
  }
</script>
